<template><section><h3>buttonSetTime 验证码验证器</h3>
<hr>
<h5>基础用法</h5>
<p>buttonSetTime 可以用来使用作为获取验证码的业务功能使用,在<code v-pre="" class="code_inline">attributes</code>和 <code v-pre="" class="code_inline">events</code>配置好属性和对应的事件回调</p>
<hr>
<demo-block>
                        <div slot="desc"><html><head></head><body><button-set-time :time="config.time" :start-msg="config.startMsg" :end-msg="config.endMsg" @click="todo"></button-set-time>

</body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">button-set-time</span>
  <span class="hljs-attr">:time</span>=<span class="hljs-string">"config.time"</span>
  <span class="hljs-attr">:start-msg</span>=<span class="hljs-string">"config.startMsg"</span>
  <span class="hljs-attr">:end-msg</span>=<span class="hljs-string">"config.endMsg"</span>
  <span class="hljs-attr">@click</span>=<span class="hljs-string">"todo"</span>
&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">button-set-time</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">config</span>: {
          <span class="hljs-attr">time</span>: <span class="hljs-number">30</span>,
          <span class="hljs-attr">startMsg</span>: <span class="hljs-string">'获取验证码'</span>,
          <span class="hljs-attr">endMsg</span>: <span class="hljs-string">'重新获取'</span>
        }
      }
    },
    <span class="hljs-attr">methods</span>: {
      todo() {
        <span class="hljs-keyword">debugger</span>
        <span class="hljs-built_in">console</span>.log(<span class="hljs-string">'事件触发'</span>)
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h5>Attributes 属性</h5>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>time</td>
<td>倒计时时间</td>
<td>Number</td>
<td>—</td>
<td>60s</td>
</tr>
<tr>
<td>startMsg</td>
<td>默认显示的提示</td>
<td>String</td>
<td>—</td>
<td>获取验证码</td>
</tr>
<tr>
<td>duringMsg</td>
<td>during 的提示</td>
<td>String</td>
<td>—</td>
<td>秒后重新获取..</td>
</tr>
<tr>
<td>endMsg</td>
<td>结束的提示</td>
<td>String</td>
<td>—</td>
<td>获取验证码</td>
</tr>
</tbody>
</table>
<h5>Events 事件</h5>
<table class="table"><thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>返回值</th>
</tr>
</thead>
<tbody>
<tr>
<td>notice-to-do</td>
<td>按钮事件</td>
<td>一</td>
</tr>
</tbody>
</table>

</section></template>

<script>
export default {
  data() {
    return {
      config: {
        time: 30,
        startMsg: '获取验证码',
        endMsg: '重新获取'
      }
    }
  },
  methods: {
    todo() {
       debugger
      console.log('事件触发')
    }
  }
}
</script>


